import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, FormControl } from '@angular/forms';
import { DataService } from 'src/app/services/data.service';
import { FileService } from 'src/app/services/file.service';


@Component({
  selector: 'app-report-config',
  templateUrl: './report-config.component.html',
  styleUrls: ['./report-config.component.scss']
})
export class ReportConfigComponent implements OnInit {

  reportCardOpened: boolean = false;

  form_1: FormGroup;
  form_2: FormGroup;
  form_3: FormGroup;
  configurationsData_1: any;
  configurationsData_2: any;
  configurationsData_3: any;


  constructor(
    private formBuilder: FormBuilder,
    private dataService: DataService,
    private fileService: FileService
  ) {


  }

  ngOnInit() {

    this.getConfigurations();

    this.form_1 = this.formBuilder.group({
      configurations_1: new FormArray([])
    });
    this.form_2 = this.formBuilder.group({
      configurations_2: new FormArray([])
    });
    this.form_3 = this.formBuilder.group({
      configurations_3: new FormArray([])
    });
  }

  private getConfigurations() {
    this.fileService.getReportConfigurations().subscribe(resp => {
      this.configurationsData_1 = resp['Matchers'];
      this.configurationsData_2 = resp['Analyzers - Report Analysis'];
      this.configurationsData_3 = resp['Analyzer - Data'];
      this.setUpConfigurationArrays();
    });
  }

  private setUpConfigurationArrays() {
    this.dataService.matchers = [];
    this.configurationsData_1.forEach(element => {
      if (element.status == true) {
        this.dataService.matchers.push(element.name);
      }
    });
    this.dataService.reportAnalysis = [];
    this.configurationsData_2.forEach(element => {
      if (element.status == true) {
        this.dataService.reportAnalysis.push(element.name);
      }
    });
    this.dataService.analyzer = [];
    this.configurationsData_3.forEach(element => {
      if (element.status == true) {
        this.dataService.analyzer.push(element.name);
      }
    });
    this.addCheckboxes();
  }

  private addCheckboxes() {
    this.configurationsData_1.forEach((o, i) => {
      const control = new FormControl(o.status === true);
      (this.form_1.controls.configurations_1 as FormArray).push(control);
    });
    this.configurationsData_2.forEach((o, i) => {
      const control = new FormControl(o.status === true);
      (this.form_2.controls.configurations_2 as FormArray).push(control);
    });
    this.configurationsData_3.forEach((o, i) => {
      const control = new FormControl(o.status === true);
      (this.form_3.controls.configurations_3 as FormArray).push(control);
    });
  }

  public setForm_1(index, name) {
    if (this.form_1.value.configurations_1[index] == true) {
      if (!this.dataService.matchers.includes(name)) {
        this.dataService.matchers.push(name);
      }
    }
    else {
      if (this.dataService.matchers.includes(name)) {
        let i = this.dataService.matchers.indexOf(name);
        if (i > -1) {
          this.dataService.matchers.splice(i, 1);
        }
      }
    }
  }
  public setForm_2(index, name) {
    if (this.form_2.value.configurations_2[index] == true) {
      if (!this.dataService.reportAnalysis.includes(name)) {
        this.dataService.reportAnalysis.push(name);
      }
    }
    else {
      if (this.dataService.reportAnalysis.includes(name)) {
        let i = this.dataService.reportAnalysis.indexOf(name);
        if (i > -1) {
          this.dataService.reportAnalysis.splice(i, 1);
        }
      }
    }
  }
  public setForm_3(index, name) {
    if (this.form_3.value.configurations_3[index] == true) {
      if (!this.dataService.analyzer.includes(name)) {
        this.dataService.analyzer.push(name);
      }
    }
    else {
      if (this.dataService.analyzer.includes(name)) {
        let i = this.dataService.analyzer.indexOf(name);
        if (i > -1) {
          this.dataService.analyzer.splice(i, 1);
        }
      }
    }
  }

  public displayOnHover() {
    let img = document.getElementsByClassName('toggle-icon4')[0] as HTMLElement;
    img.style.display = 'block';
  }

  public HideOnHover() {
    let img = document.getElementsByClassName('toggle-icon4')[0] as HTMLElement;
    img.style.display = 'none';
  }


  public toggleReportCard() {
    this.reportCardOpened = !this.reportCardOpened;

  }

  styleObject(): Object {
    if (this.reportCardOpened) {
      return { 'border-bottom-right-radius': 0, 'border-bottom-left-radius': 0 }
    }
  }

}
