import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  currentUrl: any;

  constructor(
    private router: Router,
    private dataService: DataService) {
  }

  ngOnInit() {
    this.currentUrl = this.router.url;
  }

  public navigateToHomePage() {
    this.dataService.designFile = undefined;
    this.dataService.IUPACfile = undefined;
    this.dataService.prefix = undefined;
    this.dataService.suffix = undefined;
    this.dataService.length = undefined;
    this.dataService.barcodeStart = undefined;
    this.dataService.barcodeEnd = undefined;
    this.dataService.barcodeTolerance = undefined;
    this.dataService.matchers = [];
    this.dataService.reportAnalysis = [];
    this.dataService.analyzer = [];
    this.dataService.readsExistingFiles = [];
    this.dataService.readsSelectedFiles = [];
    this.router.navigate(['/home']);
  }

}
