import { Injectable } from '@angular/core';
import { HttpHandlerService } from './http-handler.service';

@Injectable({
  providedIn: 'root'
})
export class FileService extends HttpHandlerService {

  public getReportConfigurations() {
    let path = '/get_configurations';
    return this.get(path, {});
  }

  public getExistingFiles() {
    let path = '/get_reads';
    return this.get(path, {});
  }

  public getLinks(body) {
    let path = '/get_links';
    return this.post(path, body);
  }
}
