import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-design',
  templateUrl: './design.component.html',
  styleUrls: ['./design.component.scss']
})
export class DesignComponent implements OnInit {

  designCardOpened: boolean = false;
  uploadFilesClicked: boolean = true;
  setIupacClicked: boolean = false;

  selectedFile: File = null;
  selectedIUPAC: string;
  fileTypeSelected: string = 'file';

  constructor(private dataService: DataService) {}

  ngOnInit() {
    this.dataService.designFileSelected = this.fileTypeSelected;
    this.selectedIUPAC = this.dataService.IUPACfile;
    this.selectedFile = this.dataService.designFile;
  }

  public displayOnHover(){
    let img = document.getElementsByClassName('toggle-icon1')[0] as HTMLElement;
    img.style.display = 'block';
  }

  public HideOnHover(){
    let img = document.getElementsByClassName('toggle-icon1')[0] as HTMLElement;
    img.style.display = 'none';
  }

  public toggleDesignCard() {
    this.designCardOpened = !this.designCardOpened;
  }

  public toggleUploadFiles() {
    this.uploadFilesClicked = true;
    this.setIupacClicked = false;
    this.fileTypeSelected = 'file';
    this.dataService.designFileSelected = this.fileTypeSelected;
  }

  public toggleSetIupac() {
    this.setIupacClicked = true;
    this.uploadFilesClicked = false;
    this.fileTypeSelected = 'iupac';
    this.dataService.designFileSelected = this.fileTypeSelected;
  }

  public onFileSelected(event) {
    if (!event.target.files[0].name.endsWith('.csv')) {
      document.getElementById('txt').style.color = '#e48168';
      setTimeout(() => {
        document.getElementById('txt').style.color = '#04363d';
      }, 3000);
    }
    else {
      document.getElementById('txt').style.color = '#04363d';
      this.selectedFile = event.target.files[0];
      this.dataService.designFile = this.selectedFile;
    }
  }

  public deleteFile() {
    this.selectedFile = null;
    this.dataService.designFile = null;
  }

  public setIUPACfile(event) {
    this.dataService.IUPACfile = event.target.value;
  }

  styleObject(): Object {
    if (this.designCardOpened) {
      return { 'border-bottom-right-radius': 0, 'border-bottom-left-radius': 0 }
    }
  }

}
