import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import { MatDialog } from "@angular/material";
import { ProcessingPopupComponent } from 'src/app/components/processing-popup/processing-popup.component';
import { FileService } from 'src/app/services/file.service';

@Component({
  selector: 'app-summary',
  templateUrl: './summary.component.html',
  styleUrls: ['./summary.component.scss']
})
export class SummaryComponent implements OnInit {


  constructor(
    private dataService: DataService,
    public dialog: MatDialog,
    private fileService: FileService) { }

  ngOnInit() {
    setTimeout(() => {
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }, 200);
  }


  public openDownloadDialog() {
    let processingDialog = this.dialog.open(ProcessingPopupComponent);
    processingDialog.disableClose = true;
  }

}
