import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormArray, FormControl } from '@angular/forms';
import { DataService } from 'src/app/services/data.service';
import { FileService } from 'src/app/services/file.service';
import { MatDialog } from '@angular/material';
import { CsvPopupComponent } from '../csv-popup/csv-popup.component';

@Component({
  selector: 'app-reads',
  templateUrl: './reads.component.html',
  styleUrls: ['./reads.component.scss']
})
export class ReadsComponent implements OnInit {

  readsCardOpened: boolean = false;
  csvCheckbox: boolean = false;

  existingFilesForm: FormGroup;
  existingFilesData: any;

  selectedFiles: File[] = [];

  fileTypeListSelected: string = 'existing';


  constructor(
    private formBuilder: FormBuilder,
    private dataService: DataService,
    private fileService: FileService,
    public dialog: MatDialog) {


    this.existingFilesForm = this.formBuilder.group({
      existingFiles: new FormArray([])
    });

    this.getExistingFiles();
  }

  ngOnInit() {
    this.csvCheckbox = this.dataService.readsMatches === 'Yes' ? true : false;
    this.selectedFiles = this.dataService.readsSelectedFiles;
  }

  public displayOnHover(){
    let img = document.getElementsByClassName('toggle-icon2')[0] as HTMLElement;
    img.style.display = 'block';
  }

  public HideOnHover(){
    let img = document.getElementsByClassName('toggle-icon2')[0] as HTMLElement;
    img.style.display = 'none';
  }


  public checkReadFiles(event: any) {
    this.dataService.readsMatches = this.csvCheckbox;
    this.dataService.readsMatches = this.dataService.readsMatches ? 'Yes' : 'No';
    if (this.csvCheckbox) {
      this.dataService.readsExistingFiles.forEach(file => {
        if (!file.endsWith('.csv')) {
          let dialogRef = this.dialog.open(CsvPopupComponent);
          this.existingFilesForm.reset();
          this.dataService.readsExistingFiles = [];
        }
      });
      this.dataService.readsSelectedFiles.forEach(file => {
        if (!file.name.endsWith('.csv')) {
          let dialogRef = this.dialog.open(CsvPopupComponent);
          this.selectedFiles = [];
          this.dataService.readsSelectedFiles = this.selectedFiles;
        }
      });
    }
  }


  private getExistingFiles() {
    this.fileService.getExistingFiles().subscribe(resp => {
      this.existingFilesData = resp;
      this.addCheckboxes(resp);
    });
  }
  private addCheckboxes(files: any) {
    files.forEach((o, i) => {
      const control = new FormControl(false);
      (this.existingFilesForm.controls.existingFiles as FormArray).push(control);
    });
  }

  public setExistingFiles(index, name) {

    if (!name.endsWith('.csv')) {
      if (this.csvCheckbox) {
        let dialogRef = this.dialog.open(CsvPopupComponent);
        this.existingFilesForm.reset();
        this.dataService.readsExistingFiles = [];
        return;
      }
    }

    if (this.existingFilesForm.value.existingFiles[index] == true) {
      if (!this.dataService.readsExistingFiles.includes(name)) {
        this.dataService.readsExistingFiles.push(name);
      }
    }
    else {
      if (this.dataService.readsExistingFiles.includes(name)) {
        let i = this.dataService.readsExistingFiles.indexOf(name);
        if (i > -1) {
          this.dataService.readsExistingFiles.splice(i, 1);
        }
      }
    }
  }

  public onFileSelected(event) {
    if (this.csvCheckbox == true && !event.target.files[0].name.endsWith('.csv')) {
      let dialogRef = this.dialog.open(CsvPopupComponent);
    }
    else {
      this.selectedFiles.push(event.target.files[0]);
      this.dataService.readsSelectedFiles = this.selectedFiles;
    }
  }

  public deleteFile(fileToDelete: File) {
    let i = this.selectedFiles.indexOf(fileToDelete);
    if (i > -1) {
      this.selectedFiles.splice(i, 1);
    }
    this.dataService.readsSelectedFiles = this.selectedFiles;
  }


  public toggleReadsCard() {
    this.readsCardOpened = !this.readsCardOpened;
  }

  styleObject(): Object {
    if (this.readsCardOpened) {
      return { 'border-bottom-right-radius': 0, 'border-bottom-left-radius': 0, 'border': 'none' }
    }
  }

}
