import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-processing-popup',
  templateUrl: './processing-popup.component.html',
  styleUrls: ['./processing-popup.component.scss']
})
export class ProcessingPopupComponent implements OnInit {


  links: any = {};
  sub: Subscription;
  errorMessage: any;

  displayError: boolean = false;
  displayLoader: boolean = false;
  displayLinks: boolean = false;

  constructor(
    private dataService: DataService,
    private fileService: FileService,
    private router: Router) { }

  ngOnInit() {
    this.getLinks();
  }

  private getLinks() {

    this.displayLinks = false;
    this.displayError = false;
    this.displayLoader = true;

    const formData = new FormData();
    if (this.dataService.designFile) {
      formData.append("design-file", this.dataService.designFile, this.dataService.designFile.name);
    }
    if (this.dataService.readsSelectedFiles) {
      this.dataService.readsSelectedFiles.forEach((file, i) => {
        formData.append("fastq_" + i, file, file.name);
      });
    }

    let body = {
      timeStamp: Date.now(),
      design: {
        iupac: this.dataService.designFileSelected === 'iupac' ? this.dataService.IUPACfile : undefined,
      },
      reads: {
        existingFile: this.dataService.readsExistingFiles,
        matched: this.dataService.readsMatches === 'Yes' ? true : false
      },
      libraryConfig: {
        prefix: this.dataService.prefix ? this.dataService.prefix : "",
        suffix: this.dataService.suffix ? this.dataService.suffix : "",
        length: this.dataService.length ? this.dataService.length : 0,
        length_tolerance: this.dataService.lengthTolerance ? this.dataService.lengthTolerance : 0,
        barcode_start: this.dataService.barcodeStart ? this.dataService.barcodeStart : 0,
        barcode_end: this.dataService.barcodeEnd ? this.dataService.barcodeEnd : 0,
        barcode_tolerance: this.dataService.barcodeTolerance ? this.dataService.barcodeTolerance : 0
      },
      reportConfig: {
        matchers: this.dataService.matchers,
        reportAnalysis: this.dataService.reportAnalysis,
        analysisData: this.dataService.analyzer
      }
    }

    formData.append("json", JSON.stringify(body));
    debugger
    this.sub = this.fileService.getLinks(formData).subscribe(resp => {
      if (resp) {
        this.displayLoader = false;
        this.displayError = false;
        this.displayLinks = true;

        this.links.pdf = resp.pdf;
        this.links.csv = resp.csv;
      }
    }, err => {
      this.displayLoader = false;
      this.displayLinks = false;
      this.displayError = true;
      this.errorMessage = err.message;
      console.log(this.errorMessage);
    });
  }

  public cancelRequest() {
    this.sub.unsubscribe();
  }

  public navigateToHomePage() {
    this.dataService.designFile = undefined;
    this.dataService.IUPACfile = undefined;
    this.dataService.prefix = undefined;
    this.dataService.suffix = undefined;
    this.dataService.length = undefined;
    this.dataService.barcodeStart = undefined;
    this.dataService.barcodeEnd = undefined;
    this.dataService.barcodeTolerance = undefined;
    this.dataService.readsMatches = false;
    this.dataService.matchers = [];
    this.dataService.reportAnalysis = [];
    this.dataService.analyzer = [];
    this.dataService.readsExistingFiles = [];
    this.dataService.readsSelectedFiles = [];
    this.router.navigate(['/home']);
  }
}
