import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-library-config',
  templateUrl: './library-config.component.html',
  styleUrls: ['./library-config.component.scss']
})
export class LibraryConfigComponent implements OnInit {

  libraryCardOpened: boolean = false;

  prefix: string;
  suffix: string;
  length: number;
  lengthTolerance: number;

  barcodeStart: number;
  barcodeEnd: number;
  barcodeTolerance: number;

  constructor(private dataService: DataService) { }

  ngOnInit() {
    this.prefix = this.dataService.prefix;
    this.suffix = this.dataService.suffix;
    this.length = this.dataService.length;
    this.lengthTolerance = this.dataService.lengthTolerance;
    this.barcodeStart = this.dataService.barcodeStart;
    this.barcodeEnd = this.dataService.barcodeEnd;
    this.barcodeTolerance = this.dataService.barcodeTolerance;
  }

  public displayOnHover() {
    let img = document.getElementsByClassName('toggle-icon3')[0] as HTMLElement;
    img.style.display = 'block';
  }

  public HideOnHover() {
    let img = document.getElementsByClassName('toggle-icon3')[0] as HTMLElement;
    img.style.display = 'none';
  }

  public setPrefix(event) {
    this.dataService.prefix = event.target.value;
  }

  public setSuffix(event) {
    this.dataService.suffix = event.target.value;
  }

  public setLength(event) {
    if(!event.target.value){
      this.lengthTolerance = undefined;
      this.dataService.lengthTolerance = this.lengthTolerance;

      this.barcodeStart = 0;
      this.dataService.barcodeStart = this.barcodeStart;

      this.barcodeEnd = 0;
      this.dataService.barcodeEnd = this.barcodeEnd;
    }
    this.dataService.length = parseInt(event.target.value, 10);
  }

  public setLengthTolerance(event) {
    this.dataService.lengthTolerance = parseInt(event.target.value, 10);
  }

  public toggleLibraryCard() {
    this.libraryCardOpened = !this.libraryCardOpened;
  }

  public setBarcodeStartValue(event: any) {
    this.barcodeStart = event.value
    this.dataService.barcodeStart = this.barcodeStart;
  }

  public setBarcodeEndValue(event: any) {
    this.barcodeEnd = event.value
    this.dataService.barcodeEnd = this.barcodeEnd;
  }

  public setBarcodeTolerance(event) {
    this.dataService.barcodeTolerance = parseInt(event.target.value, 10);
  }

  styleObject(): Object {
    if (this.libraryCardOpened) {
      return { 'border-bottom-right-radius': 0, 'border-bottom-left-radius': 0 }
    }
  }

}
